import * as React from "react"
import SEO from "../components/seo";
import SyntaxHighlight from "../components/SynaxHighlight";

const Customize = () => {
    return (
        <>
            <SEO title={ "Customize" }/>
            <h1>Customize</h1>
            <p>The library uses <a href="https://sass-lang.com/blog/the-module-system-is-launched" target={"_blank"}>SASS modules</a> and import the partial with the <a href="https://sass-lang.com/documentation/at-rules/use" target={"_blank"}>@use</a> for easy variable customization.</p>
            <p>The most important files to customize are:</p>
            <ul className={"pl--xl"}>
                <li className={"mb--sm"}>src/options.scss</li>
                <li className={"mb--sm"}>src/globals.scss</li>
                <li className={"mb--sm"}>src/maps.scss</li>
                <li className={"mb--sm"}>src/themes.scss</li>
            </ul>
            <p>The basic usage of @use</p>
            <SyntaxHighlight lang={ "scss" } codeString={ `@use "~skil3e-css/src/globals";
/* Or if you want to overwrite some or all the variables */
@use "~skil3e-css/src/globals" with (
  $variable: value,
  ...
);` }/>
        </>
    )
}

export default Customize
